.thumb-cover {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgba(255, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.4) 100%
  );
  border-radius: 10px;
}

.thumb,
.thumb img {
  width: 340px;
  height: 340px;
  border-radius: 10px;
}

.thumb {
  position: relative;
}

.thumb img {
  object-fit: cover;
}

.thumb h2 {
  position: absolute;
  bottom: 20px;
  left: 20px;
  font-size: 1.2rem;
  font-weight: 500;
  color: #fff;
}
